<h1 id="android-recyclerview" tabindex="-1"><a class="header-anchor" href="#android-recyclerview" aria-hidden="true">#</a> Android RecyclerView</h1>
<p>In Android, we have been able to use the <code>ListView</code> from day 1 to display a list of items vertically. However, it was not designed to display items in another way, and did not have that good support for animations, etc. When Google released their Android implementation of Material Design, they also gave us the <code>RecyclerView</code> as a new super version of the <code>ListView</code>. Let's take a look at how it works!</p>
